<template>
  <div id="adfailed">
    <head-top title="Failed To Add Device" goBack = 'finished'></head-top>
    <p>The device failed to add, please check if the network is normal or re-enter the distribution network,then retry</p>
    <img src="../../../assets/addevice/failedtoadd@3x.png" alt="">
    <div class="button" @click="$router.push({path:'/Me/choose'})">
      Retry
    </div>
  </div>
</template>
<script>
import headTop from '../../../components/common/head'

export default {
  data () {
    return {}
  },
  components: {
    headTop
  }
}
</script>
<style lang="less" scoped>
#adfailed{
  .wh(100%, 100%);
  .fs;
  z-index: 999;
  background: white;
  display: flex;
  flex-direction: column;
  p{
    font-size: 33px;
    margin-top: 97px;
    line-height: 43px;
    padding: 0 50px;
  }
  img{
    .wh(212px, 212px);
    margin-top: 106px;
    align-self: center;
    display: block;
  }
  .button{
    margin-top: 296px;
  }
}
</style>



